400;500;600;700&display=swap");
@font-face {
  font-family: "jmh";
  src: url("../src/fonts/jmh_psychedelic_caps-webfont.woff") format("woff"), url("../src/fonts/jmh_psychedelic_caps-webfont.woff2") format("woff2");
}

:root {
  --mainbg: url("../src/images/bg2.png");
  --gradient1: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.75) 0%,
    rgba(255, 75, 43, 0.75) 100%
  );
  --gradient2: linear-gradient(
    to bottom right,
    rgba(195, 20, 50, 0.75) 0%,
    rgba(36, 11, 54, 0.75) 100%
  );
  --gradient3: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.25) 0%,
    rgba(255, 75, 43, 0.25) 100%
  );
  --font1: "jmh";
  --font2: "Nova Mono", monospace;
  --font3: "Quicksand", sans-serif;
  --unit: 8px;
  --mclr: 195, 20, 50;
  --hclr: 207, 139, 243;
  --gclr: 253, 185, 155;
  --c1: 167, 112, 239;
  --c2: 207, 139, 243;
  --c3: 253, 185, 155;
  --hx1: #ff416c;
  --hx2: #c31432;
  --hx3: #240b36;
  --x4: #40e0d0;
  --x5: #ff8c00;
  --x6: #ff0080;
}

.subhead {
  font-family: var(--font4);
  margin: 2rem 0;
  text-transform: lowercase;
}

.thrivin-categories {
  width: 100%;
  font-size: calc(var(--unit) * 2);
  margin-bottom: 10rem;
}

.thrivin-categories a.cat-title {
  display: inline-block;
  font-family: var(--font3);
  font-size: 55px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.thrivin-categories a.cat-title:after {
  content: "";
  background: var(--maingrad);
  width: 70%;
  height: 6px;
  border-radius: 6px;
  display: block;
  transition: 0.25s all 0.05s ease;
}

.thrivin-categories a.cat-title:hover:after {
  width: 100%;
}

.thrivin-categories .cat-topics {
  margin-top: 3rem;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.thrivin-categories .cat-topics .topic-wrapper {
  width: 100%;
  margin-bottom: 1rem;
  border: 1px solid #ddd;
  display: flex;
  flex-flow: row wrap;
}

.thrivin-categories .cat-topics .topic-wrapper .topic-poster {
  width: 250px;
  background: #111;
}

.thrivin-categories .cat-topics .topic-wrapper .topic-poster .topic-post-name {
  border-bottom: 1px solid #333;
  padding: 25px;
  text-align: center;
}

.thrivin-categories .cat-topics .topic-wrapper .topic-poster .topic-post-name a {
  font-family: var(--font4);
  font-size: 20px;
  letter-spacing: 1px;
  text-transform: lowercase;
  color: #fff;
  background: linear-gradient(to right, rgba(var(--mclr), 1), rgba(var(--hclr), 1), rgba(var(--gclr), 1)) no-repeat bottom left/100% 1px;
}

.thrivin-categories .cat-topics .topic-wrapper .topic-poster .topic-post-img {
  padding: 25px;
  text-align: center;
}

.thrivin-categories .cat-topics .topic-wrapper .topic-poster .topic-post-img .img-wrapper {
  display: inline-block;
  border-radius: 100%;
  border: 1px solid var(--x1);
  width: 85px;
  height: 85px;
  padding: 2px;
}

.thrivin-categories .cat-topics .topic-wrapper .topic-poster .topic-post-img .img-wrapper img {
  width: 100%;
  height: 100%;
  border-radius: 100%;
  object-fit: cover;
}

.thrivin-categories .cat-topics .topic-wrapper .topic-titles {
  width: 798px;
  background: #fff;
}

.thrivin-categories .cat-topics .topic-wrapper .topic-titles .topic-title {
  width: 100%;
  border-bottom: 1px solid #ddd;
  padding: 52px 45px;
}

.thrivin-categories .cat-topics .topic-wrapper .topic-titles .topic-title a.t-title {
  font-family: var(--font3);
  font-size: 28px;
  letter-spacing: 1px;
  text-transform: uppercase;
  display: inline-block;
}

.thrivin-categories .cat-topics .topic-wrapper .topic-titles .topic-title a.t-title:after {
  content: "";
  display: block;
  width: 70%;
  height: 6px;
  border-radius: 6px;
  background: linear-gradient(to right, rgba(var(--mclr), 1), rgba(var(--hclr), 1), rgba(var(--gclr), 1));
}

.thrivin-categories .cat-topics .topic-wrapper .topic-titles .topic-title .topic-desc {
  width: 100%;
  margin-top: 2rem;
  font-family: var(--font2);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 10px;
  color: #aaa;
}

.thrivin-categories .cat-topics .topic-wrapper .topic-titles .topic-title .topic-desc span {
  color: #111;
}

.thrivin-categories .cat-topics .topic-wrapper .topic-titles .topic-title .topic-desc i {
  background: linear-gradient(to right, rgba(var(--mclr), 1), rgba(var(--hclr), 1), rgba(var(--gclr), 1)) no-repeat bottom left/100% 1px;
}

.thrivin-categories .cat-topics .topic-wrapper .topic-titles .topic-details {
  background: #eee;
  padding: 8px 25px;
  font-family: var(--font2);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 9px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}